.Home {
    width: 100vw;
    height: 100vh;
}

.signInParent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height:100vh;
    width:100vw;

}

.signInBox {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1cm;
    row-gap: 0.5cm;
    border: thin solid rgb(180, 180, 180);
    width: fit-content;
    border-radius: 12px;
    width:10cm;
}

.inputEmail, .inputPassword{
    padding:0.3cm;
    align-items: center;
    width:7cm;
    border-radius: 6px;
    border:thin solid rgb(164, 164, 164);
}

.inputEmail:focus, .inputPassword:focus{
    outline:2px solid rgb(0, 123, 255);
}

.signInHeading{
    font-size: 20px;
    font-weight: bold;
}

.createAccount{
    font-size: 14px;
    color:rgb(0, 89, 255);
    cursor: pointer;
}

.btn{
    border:3px solid rgb(0, 136, 255);
    color:black;
    background-color: transparent;
    border-radius:6px;
    cursor:pointer;
    padding:0.2cm 0.3cm;
}

.btn:hover{
    /* transition: 1s ease; */
    background-color:rgb(0, 136, 255);
    color:rgb(255, 255, 255);
    border-radius:6px;
    cursor:pointer;
}